<style scoped>
.wrap {
  width: 100%;
  height: 100%;
}

.tale-list {
  flex: 1;
}
</style>

<template>
  <section class="wrap flex-column">
    <!--  -->
    <el-breadcrumb class="breadcrumb" separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/list/commodity' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->

    <!--  -->
    <div class="operation">
      <div class="operation-btns">
          <el-button type="primary" @click="productdAdd"></el-button>
          <!-- <el-button type="danger"  @click="productDeleteAll"></el-button> -->
      </div>
    </div>
    <!-- / -->

    <div class="tale-list">
      <el-table :data="commodities" border stripe style="min-width: 900px;" @selection-change="handleSelectionChange">
        <!-- <el-table-column type="selection"></el-table-column> -->
        <el-table-column prop="id" label="ID" sortable></el-table-column>
        <el-table-column prop="title" label=""></el-table-column>
        <el-table-column prop="attrs" label="" show-overflow-tooltip>
          <template slot-scope="scope">
            <span v-for="(tag, index) in scope.row.attrs" :key="tag.id" style="margin-right: 5px;">{{tag.title}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="price" label="">
          <template slot-scope="scope">
            <span class="price">{{scope.row.price}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="stock" label="">
          <template slot-scope="scope">
            <span>{{scope.row.stock}}</span>
          </template>        
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="text" @click="checkProductImg(scope.row.id)"></el-button>
          </template>        
        </el-table-column>
        <el-table-column prop="created_at" label=""></el-table-column>
        <el-table-column label="" width="150">
          <template slot-scope="scope">
            <el-button size="small" type="primary" @click="productEdit(scope.row)"></el-button>
            <el-button size="small" type="danger"  @click="productDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

  </section>
</template>

<script>
export default {
  data() {
    return {
      //
      waittingData: [],

      commodity_id: "",

      // form
      searchForm: {
        status: "0"
      },

      currentPage: 1,
      count: 20,

      //
      commodities: []
    };
  },

  created() {
    const id = sessionStorage.commodity_id;
    this.$api.getProducts(id, res => {
      this.commodities = res.data.data;
      // this.count = res.data.count;
    });
  },

  methods: {
    //
    checkProductImg(id) {
      sessionStorage.product_id = id
      this.$router.push({name: "commoditySpecificImages"});
    },

    //
    productdAdd() {
      this.$router.push({ name: "commoditySpecificEdit" });
    },

    /**
     * 
     * @param {array object} selected 
     */
    handleSelectionChange(selected) {
      let temp = [];
      for (let it of selected) {
        temp.push(it.id);
      }
      this.waittingData = temp;
      console.log(this.waittingData);
    },

    //
    handleSearch() {
      console.log("search");
    },

    /**
     * 
     * @param {number} index 
     * @param {object} row 
     */
    productEdit(row) {
      this.$router.push({
        name: "commoditySpecificEdit",
        params: { product: row }
      });
    },

    /**
     * 
     * @param {number} index 
     * @param {object} row 
     */
    productDelete(index, row) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$api.deletecommodities(row.id, res => {
            this.commodities.splice(index, 1);
            this.$message({
              type: "success",
              message: ""
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    }
  }
};
</script>




